html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.resaltar-texto {
  margin-top: 0;
  font-size: 4rem;
  font-weight: 700;
  color: 900;
}

.texto-gris {
  color: #626262;
}

.texto-chico {
  font-size: 1.5rem;
  text-align: start;
  color: #333333;
  font-weight: 900;
  margin-bottom: 1rem;
}

.texto-negro {
  font-weight: 700;
  color: #000000;
}

.btn-transparente {
  background-color: transparent;
  color: #FFFFFF;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border: 2px solid #FFFFFF;
}
.btn-transparente:hover {
  background-color: #FFFFFF;
  color: #0c62fb;
  cursor: pointer;
}

.btn-azul {
  background-color: #FFFFFF;
  color: #0c62fb;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border: 2px solid #0c62fb;
}
.btn-azul:hover {
  background-color: #0c62fb;
  color: #FFFFFF;
  cursor: pointer;
}

.header__contenido {
  display: flex;
  flex-direction: column;
  height: auto;
}
@media (min-width: 768px) {
  .header__contenido {
    height: 40rem;
    justify-content: space-between;
  }
}
.header__contenido h1 {
  text-align: start;
  width: 50rem;
  color: #FFFFFF;
  font-size: 4rem;
  margin: 0;
  font-weight: 700;
  display: none;
}
@media (min-width: 480px) {
  .header__contenido h1 {
    margin-top: -2rem;
    display: block;
  }
}
@media (min-width: 768px) {
  .header__contenido h1 {
    width: 60rem;
    font-size: 6rem;
    margin-top: 0;
    margin-top: 10rem;
  }
}

.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 3rem 0 0 0;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 3rem 0 0 0;
  }
}
.header__barra .header__navegacion {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}
@media (min-width: 768px) {
  .header__barra .header__navegacion {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    visibility: visible;
    opacity: 1;
  }
}

.header__navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
}
@media (min-width: 768px) {
  .header__navegacion {
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.header__navegacion .enlace {
  margin-right: 2rem;
  font-size: 1.8rem;
  color: #FFFFFF;
  font-weight: 700;
}
.header__navegacion .enlace:hover {
  cursor: pointer;
  text-decoration: underline;
}

.header__logo {
  font-size: 3.7rem;
  color: #FFFFFF;
  font-weight: 900;
}
.header__logo:hover {
  opacity: 0.5;
}

.header__buscador {
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .header__buscador {
    flex-direction: row;
    justify-content: flex-end;
  }
}
.header__buscador i {
  display: inline-block;
  margin-left: 2rem;
  margin-right: 2rem;
  color: #FFFFFF;
}
.header__buscador i:hover {
  cursor: pointer;
  opacity: 70%;
}
.header__buscador p {
  display: inline;
}

.mobile-menu {
  width: 5rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu:hover {
  cursor: pointer;
  opacity: 50%;
}

.mostrar {
  position: relative;
  visibility: visible;
  opacity: 1;
  height: auto;
  background-color: #000000;
  z-index: 1;
}
@media (min-width: 768px) {
  .mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
    background-color: transparent;
  }
}

.header {
  position: relative;
  min-height: 50rem;
  max-height: 70rem;
  overflow: hidden;
}
.header .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.header .header__video {
  width: 100%;
  display: block;
  height: 100%;
}

.que-hacemos {
  width: 50%;
  margin: 0 auto;
  margin-top: -50rem;
}
@media (min-width: 768px) {
  .que-hacemos {
    margin-top: 0;
  }
}
@media (min-width: 1024px) {
  .que-hacemos {
    width: 90%;
    margin-top: 0;
  }
}
.que-hacemos a {
  color: #0c62fb;
}
.que-hacemos a:hover {
  text-decoration: underline;
}
.que-hacemos a h2 {
  text-align: start;
  font-size: 2.2rem;
  font-weight: 900;
}
.que-hacemos .resaltar-texto {
  margin-top: 0;
  font-size: 2rem;
  font-weight: 700;
  color: 900;
}
@media (min-width: 1024px) {
  .que-hacemos .resaltar-texto {
    font-size: 4rem;
  }
}

.proyecto-tarjeta {
  position: relative;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .proyecto-tarjeta {
    margin-top: 0;
  }
}
.proyecto-tarjeta:hover {
  cursor: pointer;
}
.proyecto-tarjeta:hover .proyecto-tarjeta__contenido {
  background-color: #0c62fb;
}
.proyecto-tarjeta:hover .proyecto-tarjeta__contenido p,
.proyecto-tarjeta:hover .proyecto-tarjeta__contenido span {
  color: #FFFFFF;
}
.proyecto-tarjeta:hover .proyecto-tarjeta__contenido span {
  font-weight: 900;
}

.proyecto-tarjeta__contenido {
  position: absolute;
  z-index: 1;
  margin-top: -5rem;
  right: 0px;
  background-color: #f2f2f2;
  width: 85%;
}

.footer {
  margin-top: 2rem;
  background-color: #000000;
  padding: 2rem;
}

.seccion {
  margin-top: 5rem;
  margin-bottom: 5rem;
}
.seccion a {
  color: #0c62fb;
}
.seccion a:hover {
  text-decoration: underline;
}
.seccion a h2 {
  text-align: start;
  font-size: 2.2rem;
  font-weight: 900;
}

.plataformas {
  margin-top: 10rem;
  background-color: #f2f3f4;
  padding: 5rem;
}

.plataformas__contenedor {
  width: min(90rem, 90%);
  margin: 0 auto;
}
.plataformas__contenedor a {
  display: inline-block;
  margin-bottom: 5rem;
}
.plataformas__contenedor .plataformas__contenido {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 930px) {
  .plataformas__contenedor .plataformas__contenido {
    flex-direction: row;
    justify-content: space-between;
  }
}
.plataformas__contenedor .plataformas__contenido a {
  margin-top: 5rem;
}
.plataformas__contenedor .plataformas__logos {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 930px) {
  .plataformas__contenedor .plataformas__logos {
    flex-direction: row;
    justify-content: space-between;
  }
}
.plataformas__contenedor .plataformas__logos i {
  margin-right: 10rem;
  margin-bottom: 5rem;
  font-size: 5rem;
  color: #aeaeae;
}
.plataformas__contenedor .plataformas__logos i:hover {
  color: #626262;
  cursor: pointer;
}

.vision {
  background-color: #f2f3f4;
  padding: 10rem;
}
.vision .alinear-derecha {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: start;
}
.vision .alinear-derecha p {
  width: 90rem;
}
.vision .vision__contenido {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
}
@media (min-width: 1024px) {
  .vision .vision__contenido {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr;
    column-gap: 5rem;
  }
}
.vision .vision__contenido .blogs {
  grid-row: 1/2;
  grid-auto-rows: auto;
}

@media (min-width: 1024px) {
  .blogs {
    grid-column: 2/3;
  }
}
.blogs .entrada {
  margin-bottom: 5rem;
}
.blogs .entrada:hover .entrada__descripcion {
  background-color: #0c62fb;
  cursor: pointer;
}
.blogs .entrada .entrada__imagen1 {
  background-image: url("/build/img/blog1.jpg");
  height: 30rem;
  background-position: center center;
  background-size: cover;
}
.blogs .entrada .entrada__imagen2 {
  background-image: url("/build/img/blog2.jpg");
  height: 30rem;
  background-position: center center;
  background-size: cover;
}
.blogs .entrada .entrada__descripcion {
  margin-top: -3rem;
  padding: 0.2rem 2rem;
  width: 75rem;
  background-color: #e1e1e1;
}
.blogs .entrada .entrada__descripcion h2 {
  text-align: start;
  font-size: 2rem;
}

.sidebar {
  grid-column: 1/2;
}

.cuadro {
  display: flex;
  justify-content: end;
  margin-bottom: 10rem;
}
.cuadro .cuadro--azul {
  margin-top: -8rem;
  padding: 1rem 15rem 1rem 5rem;
  width: 90rem;
  background-color: #0c62fb;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1024px) {
  .cuadro .cuadro--azul {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.cuadro .cuadro--azul h3 {
  text-align: start;
  font-weight: 900;
  font-size: 1.5rem;
}
.cuadro .cuadro--azul p {
  color: #FFFFFF;
  width: 40rem;
}
.cuadro .cuadro--azul a {
  color: #FFFFFF;
  font-weight: 900;
  text-decoration: underline;
}

.cuadro--transparente {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 768px) {
  .cuadro--transparente {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}
.cuadro--transparente p {
  margin-top: 5rem;
}

.carousel__anterior,
.carousel__siguiente {
  position: absolute;
  display: block;
  width: 30px;
  height: 30px;
  border: none;
  cursor: pointer;
  line-height: 30px;
  text-align: center;
  background: none;
  color: #0c62fb;
  opacity: 80%;
}

.carousel__siguiente {
  margin-top: -20rem;
}

.carousel__anterior {
  margin-top: 10rem;
}

.carousel__anterior:hover,
.carousel__siguiente:hover {
  opacity: 100%;
}

.carousel__anterior {
  left: -18px;
}

.carousel__siguiente {
  right: 5px;
}

@media (min-width: 1024px) {
  .carousel__anterior {
    left: 50px;
    margin-top: 12rem;
  }
  .carousel__siguiente {
    right: 50px;
  }
}
.carousel__lista {
  overflow: hidden;
}

.carousel__elemento {
  text-align: center;
}

.carousel__indicadores .glider-dot {
  display: block;
  width: 30px;
  height: 4px;
  background: #0c62fb;
  opacity: 0.2;
  border-radius: 0;
}

.carousel__indicadores .glider-dot:hover {
  opacity: 0.5;
}

.carousel__indicadores .glider-dot.active {
  opacity: 1;
}

.quienes-somos {
  position: relative;
  width: min(62rem, 70%);
  margin: 3rem auto 10rem auto;
}
.quienes-somos .quienes-somos__video {
  position: absolute;
  left: 450px;
  margin-top: -2rem;
  width: 30rem;
  border-radius: 0% 0% 0% 80%;
}
@media (min-width: 1000px) {
  .quienes-somos .quienes-somos__video {
    left: -450px;
    margin-top: -5rem;
  }
}
.quienes-somos .imagen1 {
  position: absolute;
  left: -450px;
  margin-top: -20rem;
  width: 30rem;
  height: 40rem;
  display: none;
}
@media (min-width: 1000px) {
  .quienes-somos .imagen1 {
    display: block;
  }
}
.quienes-somos .imagen2 {
  position: absolute;
  width: 43rem;
  margin-top: -40rem;
  display: none;
}
@media (min-width: 1000px) {
  .quienes-somos .imagen2 {
    display: block;
    left: 66rem;
  }
}
.quienes-somos a {
  display: inline-block;
  margin-top: 6rem;
}