@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
   // overflow-x: hidden;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    line-height: 1.5;
}
p {
    color: v.$negro;
    font-size: 2rem;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: calc(v.$separacion / 2) 0;
    font-weight: 300;
    text-align: center;
    
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}

.resaltar-texto{
    margin-top: 0;
    font-size: 4rem;
    font-weight: v.$bold;
    color: v.$black;
}

.texto-gris{
    color:  darken($color: v.$gris, $amount: 50%);
}

.texto-chico{

    font-size: 1.5rem;
    text-align: start;
    color:v.$grisOscuro;
    font-weight: v.$black;
    margin-bottom: 1rem;
      

}
.texto-negro{
    font-weight: v.$bold;
    color:v.$negro;
}

